import { useState } from "react";
import { useTranslation } from "react-i18next";
import { useNavigate, useParams } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import AssignStaffModal from "../../../../Department/Institute/AssignStaff/AssignStaffModal";
import Modal from "../../../../optimized/Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../optimized/Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../../optimized/Ui/Border/BorderBottom";
import {
  assestsFeedUrl,
  assestsUserFinanceUrl,
} from "../../../../optimized/Services/UrlConfig/AssestsBaseUrl";
import { imageShowUrl } from "../../../../optimized/Services/UrlConfig/BaseUrl";
import CreateButton from "../../../../optimized/Ui/Button/CreateButton";
import QvipleLoader from "../../../../optimized/Loader/QvipleLoader";
import style from "../../../../optimized/UserStaffMember/FinanceManager/Income/Income.module.css";
import {
  useAssignEventManager,
  useEditEventAdnSeminar,
} from "../../../../hooks/member_tab/event-api";
import { eventAdminChangeAction } from "../../../../redux/store/event-slice";

function EventDeptModal({ hideModal, edit, librarian, onParentRefetch }) {
  const { t } = useTranslation();
  const ids = useSelector((state) => state.idChange);
  const navigate = useNavigate();
  const params = useParams();
  const [state, setState] = useState("");
  const [assignHead, setAssignHead] = useState(false);
  const [disabled, setDisabled] = useState(false);

  const [assignEventManager] = useAssignEventManager();
  const [editEventAdnSeminar] = useEditEventAdnSeminar();
  const dispatch = useDispatch();

  const eventAdminQuery = () => {
    setDisabled(true);
    assignEventManager({
      id: ids?.id,
      fActivate: {
        sid: state?._id,
      },
    }).then((res) => {
      dispatch(
        eventAdminChangeAction.eventAdminQuery({
          lid: res.data?.manager,
          sid: state?._id,
        })
      );

      setDisabled(false);
      navigate(`/q/${params?.username}/events`);
    });
  };

  const editEventAdminQuery = () => {
    if (state?._id) {
      setDisabled(true);
      editEventAdnSeminar({
        osid: librarian?._id,
        nsid: state?._id,
      }).then((res) => {
        dispatch(
          eventAdminChangeAction.eventAdminQuery({
            lid: res.data?.library,
            lhead: state?._id,
          })
        );
        onParentRefetch();
        setDisabled(false);
        hideModal();
      });
    }
  };
  const onSelect = (val) => {
    setState(val);
  };

  return (
    <>
      {assignHead ? (
        <AssignStaffModal
          assignHead={assignHead}
          headSelect={onSelect}
          title={t("secect_event_head")}
          onClose={() => setAssignHead((pre) => !pre)}
          disabledId={state?._id}
        />
      ) : (
        <Modal onClose={() => hideModal(false)}>
          <div className={style.modal_container}>
            <UserMemberContentWrapper>
              <div className={style.modal_container_outer_header}>
                <div className={style.modal_container_header}>
                  <h6>
                    {edit ? t("edit_event_manager") : t("activate_event")}
                  </h6>
                </div>
                <img
                  src={`${assestsUserFinanceUrl}/close.svg`}
                  alt="close icon"
                  onClick={() => hideModal(false)}
                />
              </div>
            </UserMemberContentWrapper>
            <BorderBottom />
            <UserMemberContentWrapper
              customStyle={{
                paddingTop: "0",
              }}
            >
              <div
                className={style.card_container_image}
                onClick={() => setAssignHead(true)}
                style={{
                  cursor: "pointer",
                }}
              >
                <img
                  alt="not found"
                  src={
                    state?.staffProfilePhoto
                      ? `${imageShowUrl}/${state.staffProfilePhoto}`
                      : `${assestsFeedUrl}/feed-user-avatar.svg`
                  }
                />
                <div className={style.card_container_inner}>
                  {!state?.staffFirstName ? (
                    <>
                      <h6>{t("assign_event_manager")}</h6>
                      <p>{t("search_from_joined_list")}</p>
                    </>
                  ) : (
                    <>
                      <h6>{`${state?.staffFirstName ?? ""} 
                    ${state?.staffMiddleName ?? ""}
                    ${state?.staffLastName ?? ""}`}</h6>
                      <p>
                        {t("index_number_1")} {state?.staffROLLNO ?? ""}
                      </p>
                    </>
                  )}
                </div>
              </div>
              <CreateButton
                label={edit ? "edit_" : "activate"}
                onAction={edit ? editEventAdminQuery : eventAdminQuery}
              />
            </UserMemberContentWrapper>
            {disabled && <QvipleLoader />}
          </div>
        </Modal>
      )}
    </>
  );
}

export default EventDeptModal;
