import { useTranslation } from "react-i18next";
import { assestsUserFinanceUrl } from "../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../Ui/Border/BorderBottom";
import CreateButton from "../../Ui/Button/CreateButton";
import Modal from "../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../UserStaffMember/FinanceManager/Income/Income.module.css";
import QvipleLoader from "../../Loader/QvipleLoader";
import { useState } from "react";
import { useSelector } from "react-redux";
import { useNavigate, useParams } from "react-router-dom";
import { useCommiteeActivateModule } from "../../Hooks/ApiHooks/SiteManager/site-manager-api";
const ActivateInstituteCommitteeManager = ({ onClose }) => {
  const { t } = useTranslation();
  const instituteId = useSelector((state) => state.idChange?.id);
  const navigate = useNavigate();
  const params = useParams();
  const [disabled, setDisabled] = useState(false);

  const [commiteeActivateModule] = useCommiteeActivateModule();

  const onActivateModule = () => {
    if (instituteId) {
      setDisabled(true);
      commiteeActivateModule({
        instituteId: instituteId,
      }).then((res) => {
        if (res.data?.iqac) {
          onClose();
          navigate(`/q/${params?.username}/institute/member/committe`, {
            state: {
              qid: res.data.iqac,
              urlOffSet: "Institute",
            },
          });
          setDisabled(true);
        }
      });
    }
  };
  //   console.info("dsfhjbsdfks", financeHead, state);

  return (
    <>
      <Modal onClose={onClose}>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6>{t("active_committee")}</h6>
              </div>
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
            }}
          >
            <CreateButton label="activate" onAction={onActivateModule} />
          </UserMemberContentWrapper>
          {disabled && <QvipleLoader />}
        </div>
      </Modal>
    </>
  );
};

export default ActivateInstituteCommitteeManager;
